<template>
  <div class="mainPage">
    <div v-if="isLogin" id="loginedPage">
    <!-------------------------------------------  ------------------------------------------------->
      <div class="search_wrapper">
        <span class="title"></span>
        <span class="searchIcon iconfont">&#xe62d;</span>
        <input type="search" class="search_input" id="filter" @keyup="filter()">
        <div class="tagSearch">
          <span class="menuIcon iconfont" @click="tagSearch()">&#xe64f;</span>
          <div class="sort_text"></div>
        </div>
      </div>
      <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit">
        <div>
          <!-----------------------------------------  --------------------------------------------------->
          <div  class="headSwiper">
            <swiper :options="swiperOption" ref="mySwiper">
              <!-- slides -->
              <swiper-slide v-for="(slideItem,index) in slideItems" class="swiperItem" :key="index">
                <img :src="slideItem" alt="swiper" @click="slide_forMore(index)">
              </swiper-slide>
              <!-- Optional controls -->
              <div class="swiper-pagination"  slot="pagination"></div>
            </swiper>
          </div>
          <!-----------------------------------------  --------------------------------------------------->
          <div class="lists" v-for="(list,index) in dataList" :key="index">
            <listItem :img_src="list.image"
                      :list_title="list.title"
                      :list_type="list.type_str"
                      :list_tags="list.tags"
                      :list_author="list.author"
                      :list_time="list.upload_time | formatDate"
                      v-on:toDetail="forMore(index)">
            </listItem>
          </div>
        </div>
      </mescroll-vue>
    </div>
    <div v-else-if="!isLogin" class="noLoginWrapper">
      /
      <router-link to="/">/</router-link>
    </div>
  </div>
</template>


<script>
  //
  import 'swiper/dist/css/swiper.css'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  //
  import listItem from './list_item/list_item'
  //
  import MescrollVue from 'mescroll.js/mescroll.vue'

  export default {
    name: "mainPage",
    components: {
      swiper,
      swiperSlide,
      listItem,
      MescrollVue
    },
    computed:{
      isLogin(){
        return  sessionStorage.getItem("isLogin");
      }
    },
    updated(){
      if(this.$route.params.selectedTags){
        let tags = this.$route.params.selectedTags;
        $('.list_item').each(function () {
          for (let i = 0 ; i < tags.length ; i++){
            if (tags.indexOf("") > -1 && tags.indexOf("") <= -1 ){
              //
              $(this).toggle(tags.length < 1 || ($(this).data('model').indexOf("") > -1 && $(this).data('model').indexOf(tags[i]) > -1));
            } else if (tags.indexOf("") <= -1 && tags.indexOf("") > -1 ) {
              //
              $(this).toggle(tags.length < 1 || ($(this).data('model').indexOf("") > -1 && $(this).data('model').indexOf(tags[i]) > -1));
            }else {
              //,,
              $(this).toggle(tags.length < 1 || $(this).data('model').indexOf(tags[i]) > -1);
              if ($(this).data('model').indexOf(tags[i]) > -1) {
                break;
              }
            }
          }
        })
      }
    },
    data() {
      return {
        listId:[],
        mescroll: null, // mescroll
        mescrollDown:{},
        mescrollUp: {
          callback: this.upCallback, // ,;  callback: function (page, mescroll) { getListData(page); }
          page: {
            num: 0, // ,0,1,callback(page)1
            size: 10 // 
          },
          lazyLoad: {
            use: true // ,false
          },
          toTop: {
            //
            src: "data:image/png;base64,", //,null,
            offset: 1000 //1000px
          },
        },
        slideItems: [],
        dataList: [],
        swiperOption: {
          loop: true,
          autoplay: true,
          pagination: {
            el: '.swiper-pagination',
          }
        }
      };
    },
    beforeRouteEnter(to, from, next) { // isBounce,beforeRouteEnter
      next(vm => {
        // mescrollref,mescroll-vuebeforeRouteEnter
        vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter() // ,,isBounce
      })
    },
    beforeRouteLeave(to, from, next) { // isBounce,beforeRouteLeave
      // mescrollref,mescroll-vuebeforeRouteEnter
      this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave(); // ,,isBounce
      next()
    },
    mounted() {
      //
      let that = this;
      this.axios.get(this.mainUrl+"/api/v1/static/slideShow").then((response)=>{
        let data_lists = response.data.data;
        for(let i = 0; i < data_lists.length; i++){
          that.slideItems.push(data_lists[i].url) ;
        }
      })
    },
    methods: {
      // mescroll,mescroll
      mescrollInit(mescroll) {
        this.mescroll = mescroll;  // this.mescroll,mescrollInit
      },

      //  page = {num:1, size:10}; num: ,1; size:,10
      upCallback(page, mescroll) {
        let that = this;
        this.axios.get(this.mainUrl+"/api/v1/data/lists",{
          params: {
            page: page.num, //
            size: page.size //
          },
          headers:{
            'Authorization':sessionStorage.getItem('token')
          }
        })
          .then((response)=> {
            console.log(response);
            let data_lists = response.data.data.data_lists;
            for(let i = 0; i < data_lists.length; i++){
              that.listId[i] = data_lists[i].id;
            }
            if (response.status === 200){
              let data = page.num === 1 ? [] : that.dataList;
              data.push(...response.data.data.data_lists);
              that.dataList = data;
              // ,
              this.$nextTick(() => {
                mescroll.endSuccess(response.data.data.data_lists.length);
              });
            } else {
              console.log("err");
              mescroll.endErr()
            }
          }).catch((err)=> {
          //,;
          errorCallback&&errorCallback();
        })
      },

      //
      filter() {
        let keyword = $('#filter').val().toLowerCase();
        if (keyword === ""){
          $('.list_item').each(function () {
            $(this).show();
          })
        }
        $('.list_item').each(function () {
          $(this).toggle(keyword.length < 1 || $(this).data('model').toLowerCase().indexOf(keyword) > -1);
        })
      },

      slide_forMore(index) {
        this.$router.push({});
      },
      forMore(index) {
        let that = this;
        sessionStorage.setItem("listId",this.listId[index])
        this.$router.push(
          {
            name: "listDetail",
            params: {
              listId: that.listId[index],
              info: that.dataList[index]
            }
          });
      },
      tagSearch() {
        this.$router.push({path: "/main/tagSearch"});
      }
    }
  }
</script>

<style lang="less" scoped>
  @mainColor:#F76968;
  .mainPage{
    font-size: .68em;
    margin: 0 auto;
    .search_wrapper{
      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      height: 1.6em;
      background-color: @mainColor;
      padding-top: .32em;
      padding-bottom: .32em;
      .title{
        font-size: .6em;
        color: white;
        margin-left: .4em;
      }
      .searchIcon{
        border: none;
        background-color: white;
        border-bottom-left-radius: .8em;
        border-top-left-radius: .8em;
        margin-left: .6em;
        color: #cccccc;
        padding-left: .4em;
        padding-top: .3em;
      }
      input{
        font-size: .6em;
        padding:.2em;
        width: 60%;
        border: none;
        border-bottom-right-radius: .8em;
        border-top-right-radius: .8em;
        outline: none;
      }
      .tagSearch{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-left: .2em;
        margin-top: .2em;
        color: white;
        .menuIcon{
          font-size: .6em;
          &:active{
            color: #e0e0e0;
          }
        }
        .sort_text{
          font-size: .36em;
        }
      }
    }
    .headSwiper{
      width: 100%;
      height: 26%;
      z-index: -1;
      img{
        width: 100%;
        height: 6em;
      }
      /deep/ .swiper-pagination-bullet-active{
        background-color: @mainColor;
      }
    }

    .noLoginWrapper{
      font-size: .6em;
      margin-top: 10em;
      margin-left: 1em;
    }

    .mescroll {
      position: fixed;
      padding-bottom: 1rem;
      top: 1.6em;
      bottom: 0;
      height: auto;
    }
  }
</style>



